html, body {
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: #062635;
}

.app {
    position: relative;
    display: grid;
    grid-template-columns: auto;
    height: 100%;
    width: 100%;
    background-color: #062635;
}

.dual-grid {
    display: grid;
    grid-template-columns: auto auto;
}

form input[type=text], form input[type=password], form input[type=number] {
    border: solid 1px rgba(0, 0, 0, 0.4);
}

.page {
    position: relative;
    top: 0;
    width: 100%;
    height: auto;
    bottom: 0px;
    background-color: #062635;
    padding-bottom: 2%;
    bottom: 0;
}

.inner-content {
    position: relative;
    display: block;
    min-height: 80%;
    width: 96%;
    margin: 2% 0% 0% 2%;
    border-radius: 3rem;
    border: solid 2px #49a;
    box-shadow: 0px 0px 1px 3px rgba(200, 220, 255, 0.8);
    background-color: #eee;
}

@import url('https://fonts.googleapis.com/css?family=Oswald|Roboto');

h1,h2 {
    font-family: 'Oswald', sans-serif;
    font-weight: bold;
}

p,a,span {
    font-family: 'Roboto';
}

