@import url(https://fonts.googleapis.com/css?family=Oswald|Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: #062635;
}

.app {
    position: relative;
    display: grid;
    grid-template-columns: auto;
    height: 100%;
    width: 100%;
    background-color: #062635;
}

.dual-grid {
    display: grid;
    grid-template-columns: auto auto;
}

form input[type=text], form input[type=password], form input[type=number] {
    border: solid 1px rgba(0, 0, 0, 0.4);
}

.page {
    position: relative;
    top: 0;
    width: 100%;
    height: auto;
    bottom: 0px;
    background-color: #062635;
    padding-bottom: 2%;
    bottom: 0;
}

.inner-content {
    position: relative;
    display: block;
    min-height: 80%;
    width: 96%;
    margin: 2% 0% 0% 2%;
    border-radius: 3rem;
    border: solid 2px #49a;
    box-shadow: 0px 0px 1px 3px rgba(200, 220, 255, 0.8);
    background-color: #eee;
}

h1,h2 {
    font-family: 'Oswald', sans-serif;
    font-weight: bold;
}

p,a,span {
    font-family: 'Roboto';
}


.store-list {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
    justify-items: center;
    padding-bottom: 3rem;
}


@media only screen and (max-width: 1280px) {
    .store-list {
        grid-template-columns: repeat(3, auto);
    }
}

@media only screen and (max-width: 960px) {
    .store-list {
        grid-template-columns: auto auto;
    }
}

@media only screen and (max-width: 640px) {
    .store-list {
        grid-template-columns: auto;
    }
}

.rent-page h1 {
    text-align: center;
    padding-top: 2rem;
    margin-top: 0;
}

.store {
    position: relative;
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 1rem;
    align-items: center;
    border: solid 1px #aaa;
    border-radius: 2rem;
    box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.4);
    width: 15rem;
    height: 8rem;
    padding: 1rem;
    background-color: #fff;
}

.store h2 {
    font-size: 1.2rem;
    margin-top: 0;
}

.store-icon {
    font-size: 3rem;
}

.image-container {
    position: fixed;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    z-index: 1000;
    display: none;
    text-align: center;
    vertical-align: middle;
    background-color: rgba(6, 38, 53, 0.8);
}

.image-container.visible {
    display: block;
    cursor: zoom-out;
}

.store-image-big {
    position: fixed;
    max-height: 90%;
    max-width: 100%;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    -webkit-animation: fade-in 0.5s linear;
            animation: fade-in 0.5s linear;
}

@-webkit-keyframes fade-in {
    0% { opacity: 0 }
    100% { opacity: 1 }
}

@keyframes fade-in {
    0% { opacity: 0 }
    100% { opacity: 1 }
}

.store-image {
    width: 4rem;
    height: auto;
    cursor: zoom-in;
    border-radius: 50%;
}

.store-info {
    display: grid;
    grid-template-columns: auto auto;
    align-items: flex-end;
    margin-bottom: 0.2rem;
}

.store-info span:first-child {
    width: 5rem;
}

.store-info span:nth-child(2) {
    text-align: right;
}

.store-info:last-child span:first-child {
    width: 8rem;
}

.hidden {
    display: none;
}


/* common */
.ribbon {
    width: 100px;
    height: 100px;
    overflow: hidden;
    position: absolute;
}

.ribbon::before,
.ribbon::after {
    position: absolute;
    z-index: 100;
    content: '';
    display: block;
    border: 5px solid #2980b9;
}

.ribbon.orange::before,
.ribbon.orange::after {
    border: 5px solid #b98029;
    border-top-color: transparent;
    border-right-color: transparent;
}
.ribbon span {
    position: absolute;
    display: block;
    width: 142px;
    padding: 8px 0;
    background-color: #3498db;
    box-shadow: 0 5px 10px rgba(0,0,0,.3);
    color: #fff;
    font: 700 10px/2 'Roboto', sans-serif;
    text-shadow: 0 1px 1px rgba(0,0,0,.2);
    text-transform: uppercase;
    text-align: center;
}

.ribbon.orange span {
    background-color: #db9834;
}

/* top left*/
.ribbon-top-left {
    top: -10px;
    left: -10px;
}
.ribbon-top-left::before,
.ribbon-top-left::after {
    border-top-color: transparent;
    border-left-color: transparent;
}
.ribbon-top-left::before {
    top: 0;
    right: 0;
}
.ribbon-top-left::after {
    bottom: 0;
    left: 0;
}
.ribbon-top-left span {
    right: -12px;
    top: 15px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

/* top right*/
.ribbon-top-right {
    top: -10px;
    right: -10px;
}
.ribbon-top-right::before,
.ribbon-top-right::after {
    border-top-color: transparent;
    border-right-color: transparent;
}
.ribbon-top-right::before {
    top: 0;
    left: 0;
}
.ribbon-top-right::after {
    bottom: 0;
    right: 0;
}
.ribbon-top-right span {
    left: -10px;
    top: 18px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

/* bottom left*/
.ribbon-bottom-left {
    bottom: -10px;
    left: -10px;
}
.ribbon-bottom-left::before,
.ribbon-bottom-left::after {
    border-bottom-color: transparent;
    border-left-color: transparent;
}
.ribbon-bottom-left::before {
    bottom: 0;
    right: 0;
}
.ribbon-bottom-left::after {
    top: 0;
    left: 0;
}
.ribbon-bottom-left span {
    right: -25px;
    bottom: 30px;
    -webkit-transform: rotate(225deg);
            transform: rotate(225deg);
}

/* bottom right*/
.ribbon-bottom-right {
    bottom: -10px;
    right: -10px;
}
.ribbon-bottom-right::before,
.ribbon-bottom-right::after {
    border-bottom-color: transparent;
    border-right-color: transparent;
}
.ribbon-bottom-right::before {
    bottom: 0;
    left: 0;
}
.ribbon-bottom-right::after {
    top: 0;
    right: 0;
}
.ribbon-bottom-right span {
    left: -25px;
    bottom: 30px;
    -webkit-transform: rotate(-225deg);
            transform: rotate(-225deg);
}

.content-wrapper {
    margin: 2rem;
    overflow: hidden;
}

.content-wrapper p {
    line-height: 1.2;
}

.content-wrapper p a{
    color: #186ea5;
    font-weight: bold;
    text-decoration: none;
}

.content-wrapper p a:hover{
    color: #0c3f5f;
}

.content-wrapper p:last-child {
    padding-bottom: 1rem;
}

sup, sub {
    vertical-align: baseline;
    position: relative;
    top: -0.4em;
}

sub { 
    top: 0.4em; 
}

.airphoto {
    display: inline;
    width: 50%;
    max-height: 100%;
    height: auto;
    float: left;
    padding: 10px;
    margin-right: 2rem;
    margin-bottom: 2rem;
    border-radius: 2rem;
}

@media only screen and (max-width: 960px) {
    .airphoto {
        width: 100%;
    }
}

.contact-page .inner-content {
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
    align-items: center;
    justify-items: center;
    padding: 3rem 0 3rem 0;
}

@media only screen and (max-width: 980px) {
    .contact-page .inner-content {
        grid-template-columns: auto;
        grid-column-gap: 0.5rem;
        grid-row-gap: 0.5rem;
    }
}

.contact-page .inner-content > div {
    display: block;
    position:relative;
}

.map {
    display: block;
}

.map iframe {
    z-index: -1;
    box-shadow: 0px 0px 20px 5px rgba(0, 0, 0, 0.4);
}

.address {
    line-height: 1.5;
    font-size: 1.3rem;
    margin-left: 0.5rem;
    padding: 1rem;
    color: #333;
}

.address h2 {
    font-size: 2rem;
    margin-top: 0;
}


.contact-page .inner-content > div.namecard {
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 1rem;
    justify-items: center;
    align-items: center;
    height: 8rem;
    border-radius: 2rem;
    color: #333;
    border: 2px solid #aaa;
    background-color: #eee;
    box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.7);
    width: 27rem;
}

@media only screen and (max-width: 500px) {
    .contact-page .inner-content > div.namecard {
        width: 21rem;
        grid-template-columns: auto;
    }

    .contact-page .inner-content > div.map {
        display: none;
    }
    
    .avatar {
        display: none;
    }
}

.namecard .person-contacts {
    line-height: 1.5;
    margin-right: 0.5rem;
}

.namecard a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
}

.namecard h2 {
    margin: 0 0 0.5rem 0;
}

.avatar {
    height: 6rem;
    width: 6rem;
    border-radius: 50%;
    font-size: 6rem;
    box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.8);
    margin-left: 1rem;
    background-color: #def;
}

.avatar svg {
    padding: 0;
    position: absolute;
}

.avatar img {
    width: 6rem;
    height: auto;
    border-radius: 50%;
}

span.icon {
    width: 2rem;
    display: inline-block;
    text-align: center;
}

.main-header {
    position: relative;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 5rem;
    background-color: #062635;
    z-index: 1000;
}

.language-selector {
    position: absolute;
    top: 1rem;
    right: 1rem;
}

.flag {
    height: 1.35rem;
    cursor: pointer;
    width: 2rem;
    margin: 0.5rem;
    background-size: cover;
    display: inline-block;
}

.main-menu li.hamburger-language-selector {
    display: none;
}


.main-header .title {
    display: inline;
    margin: 1rem;
    height: 3rem;
    width: auto;
}

.main-menu {
    position: absolute;
    top: 2rem;
    left: 22rem;
    display: inline-block;
    list-style-type: none;
    margin: 0 0 0 3rem;
    padding: 0;
    bottom: 0px;
    height: 2rem;
    overflow: hidden;
    vertical-align: top;
}

.main-menu li {
    float: left;
    vertical-align: middle;
    display: table;
    height: 100%;
    border-right: solid 1px rgba(255, 255, 255, 0.3);
    margin: 0 0.03rem 0 0.03rem; 
}

.main-menu li:first-child {
    border-left: solid 1px rgba(255, 255, 255, 0.3);
}
.main-menu li a {
    display: block;
    color: white;
    font-weight: bold;
    text-align: center;
    vertical-align: middle;
    padding-left: 1rem;
    padding-right: 1rem;
    height: 100%;
    display: table-cell;
    text-decoration: none;
}

.main-menu li a:hover {
    text-shadow: 0px 0px 10px #eef;
}

.hamburger-menu {
    display: none;
}

@media only screen and (max-width: 920px) {

    .main-header .title {
        height: auto;
        width: 75%;
        max-width: 20rem;
    }

    .main-menu.invisible {
        display: none;
    }

    .main-menu.visible {
        position: absolute;
        display: block;
        right: 2rem;
        z-index: 1000;
        width: 100%;
        top: 5rem;
        left: 0;
        margin: 0;
        overflow: visible;
        overflow: initial;
    }

    .main-menu.visible li {
        float: none;
        float: initial;
        width: 100%;
        left: 0px;
        margin: 0;
        border: none;
        border-top: solid 1px #000;
        background-color: #062635;
        font-size: 1.4rem;
        margin-bottom: -1px;
    }

    .main-menu li.hamburger-language-selector {
        display: block;
        text-align: center;
        height: auto;
    }

    .flag {
        display: inline-block;
        position: relative;
        height: 1.4rem;
        margin: 1rem;
    }

    .main-menu.visible li a {
        padding: 1rem;
    }

    .main-menu.visible li:last-child {
        border-bottom: solid 1px #000;
    }

    .hamburger-menu {
        position: absolute;
        display: inline-block;
        font-size: 2.5rem;
        color: #fff;
        right: 2rem;
        margin-top: 1rem;
        cursor: pointer;
    }

    .hamburger-menu svg {
        cursor: pointer;
    }

    .language-selector {
        display: none;
    }
}

