.store-list {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
    justify-items: center;
    padding-bottom: 3rem;
}


@media only screen and (max-width: 1280px) {
    .store-list {
        grid-template-columns: repeat(3, auto);
    }
}

@media only screen and (max-width: 960px) {
    .store-list {
        grid-template-columns: auto auto;
    }
}

@media only screen and (max-width: 640px) {
    .store-list {
        grid-template-columns: auto;
    }
}

.rent-page h1 {
    text-align: center;
    padding-top: 2rem;
    margin-top: 0;
}

.store {
    position: relative;
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 1rem;
    align-items: center;
    border: solid 1px #aaa;
    border-radius: 2rem;
    box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.4);
    width: 15rem;
    height: 8rem;
    padding: 1rem;
    background-color: #fff;
}

.store h2 {
    font-size: 1.2rem;
    margin-top: 0;
}

.store-icon {
    font-size: 3rem;
}

.image-container {
    position: fixed;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    z-index: 1000;
    display: none;
    text-align: center;
    vertical-align: middle;
    background-color: rgba(6, 38, 53, 0.8);
}

.image-container.visible {
    display: block;
    cursor: zoom-out;
}

.store-image-big {
    position: fixed;
    max-height: 90%;
    max-width: 100%;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    animation: fade-in 0.5s linear;
}

@keyframes fade-in {
    0% { opacity: 0 }
    100% { opacity: 1 }
}

.store-image {
    width: 4rem;
    height: auto;
    cursor: zoom-in;
    border-radius: 50%;
}

.store-info {
    display: grid;
    grid-template-columns: auto auto;
    align-items: flex-end;
    margin-bottom: 0.2rem;
}

.store-info span:first-child {
    width: 5rem;
}

.store-info span:nth-child(2) {
    text-align: right;
}

.store-info:last-child span:first-child {
    width: 8rem;
}

.hidden {
    display: none;
}
