.contact-page .inner-content {
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
    align-items: center;
    justify-items: center;
    padding: 3rem 0 3rem 0;
}

@media only screen and (max-width: 980px) {
    .contact-page .inner-content {
        grid-template-columns: auto;
        grid-column-gap: 0.5rem;
        grid-row-gap: 0.5rem;
    }
}

.contact-page .inner-content > div {
    display: block;
    position:relative;
}

.map {
    display: block;
}

.map iframe {
    z-index: -1;
    box-shadow: 0px 0px 20px 5px rgba(0, 0, 0, 0.4);
}

.address {
    line-height: 1.5;
    font-size: 1.3rem;
    margin-left: 0.5rem;
    padding: 1rem;
    color: #333;
}

.address h2 {
    font-size: 2rem;
    margin-top: 0;
}


.contact-page .inner-content > div.namecard {
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 1rem;
    justify-items: center;
    align-items: center;
    height: 8rem;
    border-radius: 2rem;
    color: #333;
    border: 2px solid #aaa;
    background-color: #eee;
    box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.7);
    width: 27rem;
}

@media only screen and (max-width: 500px) {
    .contact-page .inner-content > div.namecard {
        width: 21rem;
        grid-template-columns: auto;
    }

    .contact-page .inner-content > div.map {
        display: none;
    }
    
    .avatar {
        display: none;
    }
}

.namecard .person-contacts {
    line-height: 1.5;
    margin-right: 0.5rem;
}

.namecard a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
}

.namecard h2 {
    margin: 0 0 0.5rem 0;
}

.avatar {
    height: 6rem;
    width: 6rem;
    border-radius: 50%;
    font-size: 6rem;
    box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.8);
    margin-left: 1rem;
    background-color: #def;
}

.avatar svg {
    padding: 0;
    position: absolute;
}

.avatar img {
    width: 6rem;
    height: auto;
    border-radius: 50%;
}

span.icon {
    width: 2rem;
    display: inline-block;
    text-align: center;
}
