.content-wrapper {
    margin: 2rem;
    overflow: hidden;
}

.content-wrapper p {
    line-height: 1.2;
}

.content-wrapper p a{
    color: #186ea5;
    font-weight: bold;
    text-decoration: none;
}

.content-wrapper p a:hover{
    color: #0c3f5f;
}

.content-wrapper p:last-child {
    padding-bottom: 1rem;
}

sup, sub {
    vertical-align: baseline;
    position: relative;
    top: -0.4em;
}

sub { 
    top: 0.4em; 
}

.airphoto {
    display: inline;
    width: 50%;
    max-height: 100%;
    height: auto;
    float: left;
    padding: 10px;
    margin-right: 2rem;
    margin-bottom: 2rem;
    border-radius: 2rem;
}

@media only screen and (max-width: 960px) {
    .airphoto {
        width: 100%;
    }
}
